.faq_active .answer {
    animation: fade 0.6s ease-in-out;
    max-height: 580px;
}
.question div > img {
    transition: transform 0.7s;
}
.faq_active > div > img {
    transform: rotate(45deg);
}
.answer {
    transition-timing-function: ease;
}

@keyframes fade {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}